import React from 'react';

export default class RangeInput extends React.Component {
    dragBead() {
        
    }
    render() {
        return (
            <div
                className="input__range">
                <div
                    className="input__range--bead"
                    onClick={this.dragBead.bind(this)}></div>
                <div className="input__range--track"></div>
            </div>
        );
    }
}